@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  list-style-type: none;
  outline: none;
}

img {
  max-width: 100%;
  display: block;
}

.revolution {
  max-width: 1200px;
  margin: 0 auto;
  padding: 48px 0 70px 0;
  text-align: center;
}
.revolution__main {
  max-width: 906px;
  margin: 0 auto;
  margin-top: 66px;
}
.revolution__head {
  font-weight: 400;
  font-size: 14px;
  color: #848e97;
  line-height: 15px;
}
.revolution__title {
  font-weight: 700;
  font-size: 54px;
  color: #635c73;
  line-height: 54px;
}
.revolution__paragraph {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 21px;
  color: #323232;
  line-height: 32px;
  margin-top: 20px;
}
.revolution__btns {
  display: flex;
  width: 365px;
  flex-wrap: wrap;
  gap: 15px;
  align-items: center;
  margin: 0 auto;
  margin-top: 13px;
}
.revolution__btns button {
  padding: 5px;
  font-weight: bold;
  border: 1px solid lightgrey;
  cursor: pointer;
}
.revolution__btns span {
  border: 1px solid lightgrey;
  padding: 5px;
  font-size: 13px;
  margin-left: -5px;
  font-weight: 700;
}

@media screen and (max-width: 481px) and (min-width: 320px) {
  .revolution {
    padding: 10px;
  }
  .revolution__title {
    font-weight: 700;
    font-size: 26px;
    color: #635c73;
    line-height: 28px;
  }
  .revolution__paragraph {
    font-weight: 400;
    font-size: 14px;
    color: #323232;
    line-height: 20px;
  }
  .revolution__btns {
    width: 267px;
    text-align: center;
    justify-content: center;
  }
}
@media screen and (max-width: 993px) and (min-width: 481px) {
  .revolution {
    padding: 10px;
  }
  .revolution__title {
    font-weight: 700;
    font-size: 42px;
    color: #635c73;
    line-height: 42px;
  }
}
.here {
  margin: 0 auto;
  max-width: 1200px;
  padding-left: 10px;
  padding-right: 10px;
  font-style: normal;
}
.here__container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(1fr, 1fr));
  grid-column-gap: 39px;
  padding-top: 100px;
  padding-bottom: 33px;
}
@media (max-width: 1150px) {
  .here__container {
    grid-column-gap: 0;
  }
}
.here__content {
  grid-area: 1/1/3/2;
}
.here__block-name {
  grid-area: 1/2/2/3;
}
.here__window-img {
  grid-area: 2/2/3/3;
}
.here__title-text {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 46px;
  line-height: 35px;
  color: #8d81ac;
  padding-left: 18px;
  text-align: center;
}
@media (max-width: 1250px) {
  .here__title-text {
    font-size: 41px;
  }
}
@media (max-width: 388px) {
  .here__title-text {
    padding: 0 30px 36px 30px;
  }
}
@media (max-width: 420px) {
  .here__title-text {
    padding: 0 60px 36px 60px;
  }
}
.here__card-content {
  display: flex;
}
.here__card-text-container {
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  gap: 17px;
  max-width: 405px;
  transition: 2s;
}
@media (max-width: 1100px) {
  .here__card-text-container {
    max-width: 300px;
    transition: 2s;
  }
}
@media (max-width: 1150px) {
  .here__card-text-container {
    max-width: 270px;
    transition: 2s;
  }
}
.here__card-name {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 16px;
  color: #323232;
}
.here__card-information {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #848e97;
}
.here__window-img-320 {
  display: none;
}

.first_img {
  margin-right: 23px;
}

.second_img {
  margin-right: 44px;
}

.third_img {
  margin-right: 63px;
  margin-left: 10px;
}

.fourth_img {
  margin-right: 50px;
  margin-left: 10px;
}

@media (max-width: 993px) {
  .here__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 0px;
    padding-top: 62px;
  }
  .here__content {
    max-width: 700px;
  }
  .here__title-text {
    font-size: 42px;
    line-height: 44px;
    padding: 0 0 50px 0;
    order: -1;
  }
}
@media (max-width: 993px) and (max-width: 560px) {
  .here__title-text {
    font-size: 36px;
  }
}
@media (max-width: 993px) {
  .here__card-content {
    padding-left: 20px;
    padding-right: 20px;
  }
  .here__card-information {
    max-width: 563px;
  }
  .here__card-text-container {
    max-width: 100%;
  }
  .here__img-container {
    margin: 0 auto;
  }
  .here__window-img {
    padding-right: 0px;
  }
  .here__window-container {
    margin-bottom: 0px;
  }
}
@media (max-width: 481px) {
  .here__title-text {
    font-size: 28px;
    line-height: 38px;
    padding: 0 50px 36px 50px;
    text-align: center;
    order: -1;
  }
  .here__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .here__card-content {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    align-items: center;
  }
  .here__card-img {
    margin-bottom: 20px;
    margin-right: 0;
    margin-left: 0;
  }
  .here__card-name {
    text-align: center;
  }
  .here__card-information {
    text-align: center;
    min-width: 271px;
  }
  .here__window-img {
    display: none;
  }
  .here__window-img-320 {
    display: block;
  }
}
.top {
  padding-top: 32px;
}

.fork {
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
  max-width: 1200px;
}
.fork__container {
  max-width: 1100px;
  margin: 0 auto;
  position: relative;
  z-index: 90;
}
.fork__cards {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
@media (max-width: 650px) {
  .fork__cards {
    gap: 10px;
  }
}
.fork__title-name {
  font-family: "Montserrat", sans-serif;
  color: #FFFFFF;
  font-size: 48px;
  line-height: 42px;
  font-weight: 700;
  padding: 100px 0px 61px 0px;
  text-align: center;
}
.fork__card-single {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 97px;
}
.fork__card-name {
  font-family: "Montserrat", sans-serif;
  color: #FFFFFF;
  font-size: 24px;
  font-weight: 400;
  text-align: center;
  padding-top: 36px;
  padding-bottom: 36px;
  width: 250px;
  background-color: #695b6a;
}
@media (max-width: 1200px) {
  .fork__card-name {
    width: 228px;
  }
}
@media (max-width: 993px) {
  .fork__card-name {
    width: 250px;
  }
}
@media (max-width: 650px) {
  .fork__card-name {
    width: 218px;
  }
}
@media (max-width: 481px) {
  .fork__card-name {
    width: 250px;
  }
}
.fork__card-information {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Montserrat", sans-serif;
  color: #FFFFFF;
  background-color: #5f5161;
  width: auto;
  height: 460px;
}
.fork__card-price {
  font-size: 60px;
  font-weight: 700;
  padding: 30px 0px 20px 0px;
}
.fork__card-time {
  font-size: 16px;
  font-weight: 400;
  padding-bottom: 38px;
}
.fork__card-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  text-align: center;
  padding: 30px 0px 36px;
}
.fork__card-btn {
  font-family: "Montserrat", sans-serif;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 400;
  padding: 15px 38px;
  margin-bottom: 39px;
  background-color: #776b79;
  border: 1px solid #776b79;
  border-radius: 40px;
}

.card-name-another {
  width: 299px;
  padding-top: 43px;
  padding-bottom: 43px;
  background-color: #3f2c41;
}
@media (max-width: 1200px) {
  .card-name-another {
    width: 255px;
  }
}
@media (max-width: 993px) {
  .card-name-another {
    width: 299px;
  }
}
@media (max-width: 650px) {
  .card-name-another {
    width: 245px;
  }
}
@media (max-width: 481px) {
  .card-name-another {
    width: 299px;
  }
}

.card-price-another {
  font-size: 82px;
  font-weight: 400;
  padding-top: 14px;
  padding-bottom: 16px;
}

.card-time-another {
  padding-bottom: 46px;
}

.btn-another {
  margin-bottom: 47px;
}

.card-details-another {
  padding-top: 40px;
  padding-bottom: 83px;
}

.card-information-another {
  background-color: #473249;
  width: auto;
  height: 560px;
}

.card-details-another {
  padding: 40px 0px 83px 0px;
}

@media (max-width: 993px) {
  .fork__cards {
    max-width: 600px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    gap: 25px;
    margin-bottom: 94px;
  }
  .fork__title-name {
    font-size: 42px;
    line-height: 44px;
    padding-bottom: 39px;
  }
  .fork__card-single {
    padding-bottom: 0px;
  }
  .fork__content {
    display: flex;
    justify-content: center;
  }
}
@media (max-width: 481px) {
  .fork {
    font-style: normal;
  }
  .fork__cards {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 18px;
  }
  .fork__title-name {
    font-size: 28px;
    line-height: 42px;
    padding: 14px 0px 32px 0px;
  }
}
.first-card-padding {
  padding-bottom: 54px;
}